<template>
  <div class="box">
    <img :src="story.header" alt class="head-portrait" />
    <span
      class="icon-position"
      :class="followago"
      v-show="follow"
      @click="followToggle"
    ></span>
    <transition name="myAnimate">
      <span
        class="icons-position"
        :class="follow ? null : followback"
        v-show="follow"
      ></span>
    </transition>
    <transition name="myAnimate">
      <span class="text-position" v-show="follow">{{
        !follow ? "已关注" : ""
      }}</span>
    </transition>
    <span class="font-style">{{ story.name }}</span>
    <span class="font-style">{{ story.passtime }}</span>
    <br />
    <span class="text-box">{{ story.text }}</span>
    <img v-if="story.images" :src="story.images" alt class="display-file" />
    <video
      v-if="story.video"
      width="100%"
      height="240"
      controls
      class="set-video-cover"
    >
      <source :src="story.video" type="video/mp4" />
      Your browser does not support HTML5 video.
    </video>
    <span
      class="footer-font"
      @click="giveUp"
      :class="!story.down ? upback : upago"
      v-if="story.down"
      >{{ story.up }}</span
    >
    <transition name="myAnimate">
      <span
        class="footer-font"
        @click="giveUp"
        :class="!story.down ? upback : upago"
        v-if="!story.down"
        >{{ story.up }}</span
      >
    </transition>
    <div class="box-position">
      <textarea
        name=""
        id=""
        cols="40"
        rows="1.5"
        placeholder="评论..."
        v-model="texts"
      ></textarea
      ><button @click="publish">发表</button>
    </div>
    <div class="comments-position-box" v-show="story.top_comments_content">
      <img :src="story.top_comments_header" alt class="head-portrait" />
      <span class="comments-font-style">{{ story.top_comments_name }}</span>
      <span class="comments-font-styles">{{ story.top_comments_content }}</span>
    </div>
    <ul>
      <li v-for="item in newArr" :key="item.id">
        <br />
        <transition name="lianimate">
          <div class="comments-position-box" @click="deleteFollow(item)">
            <img :src="item.headerImg" alt class="head-portrait" />
            <span class="comments-font-style">{{ item.username }}</span>
            <span class="comments-font-styles">{{ item.text }}</span>
            <span
              class="delete-follow"
              v-show="item.deleteFollows"
              @click="removComments(item, item.id)"
              >删除评论</span
            >
          </div>
        </transition>
      </li>
    </ul>
    <div style="height: 200px"></div>
  </div>
</template>
<script>
export default {
  props: ["headerImg", "history"],
  data() {
    return {
      story: {
        text: this.$route.query.text,
        header: this.$route.query.header,
        name: this.$route.query.name,
        passtime: this.$route.query.passtime,
        images: this.$route.query.images,
        top_comments_name: this.$route.query.top_comments_name,
        top_comments_header: this.$route.query.top_comments_header,
        top_comments_content: this.$route.query.top_comments_content,
        up: this.$route.query.up,
        type: this.$route.query.type,
        imgurl: this.$route.query.imgurl,
        video: this.$route.query.video,
        sid: this.$route.query.sid,
        uid: this.$route.query.uid,
        down: this.$route.query.down,
        gz: false,
        dz: !this.$route.query.sid,
        pl: null,
        plct: false,
        date: "",
      },
      upago: "icon-dianzan",
      upback: "icon-dianzan_1",
      follow: true,
      bool: true,
      followago: "icon-guanzhu",
      followback: "icon-zhengque",
      texts: "",
      deleteFollows: false,
      newArr: [],
      count: 0,
    };
  },
  beforeDestroy() {
    for (let i = 0; i < this.history.length; i++) {
      if (this.history[i].sid === this.story.sid) {
        this.bool = false;
      }
    }
    if (this.bool) {
      localStorage.setItem("story", JSON.stringify(this.story));
      this.$emit("chuanzi");
    }
  },
  methods: {
    giveUp() {
      if (this.story.down) {
        //隐式转换string为number
        this.story.up -= 0;
        this.story.up += 1;
        this.story.down = false;
        this.story.dz = true;
        console.log(221);
      } else {
        this.story.down = true;
        this.story.up -= 1;
        this.story.dz = false;
      }
    },
    publish() {
      if (this.texts === "") {
        alert("没有内容！");
        console.log(this.story.dz);
      } else {
        let oneArr = {
          text: this.texts,
          username: (this.username = JSON.parse(
            sessionStorage.userInfo
          ).username),
          headerImg: this.headerImg,
          id: (this.count += 1),
          deleteFollows: this.deleteFollows,
          plct: this.story.plct,
        };
        this.newArr.push(oneArr);
        console.log(this.newArr);
        this.story.pl = this.texts;
        this.story.plct = true;
        this.story.date = new Date();
        this.texts = "";
      }
    },
    followToggle() {
      this.follow = false;
      this.story.gz = true;
    },
    deleteFollow(item) {
      if (item.deleteFollows) {
        item.deleteFollows = false;
      } else {
        item.deleteFollows = true;
      }
    },
    removComments(item, id) {
      this.newArr = this.newArr.filter((t) => t.id !== id);
    },
  },
  created() {
    for (let i = 0; i < this.history.length; i++) {
      if (this.history[i].uid === this.story.uid) {
        this.follow = false;
      }
    }
  },
};
</script>
<style lang="scss" scoped>
.box {
  position: relative;
  margin-top: -40px;
  padding: 10px;
  width: 100%;
  height: 100%;
  background-color: #fff;

  .myAnimate-leave-active,
  .myAnimate-enter-active {
    transition: all 0.5s linear;
  }

  .myAnimate-leave-to {
    transform: translateY(-30px);
  }

  .myAnimate-enter {
    transform: translateX(-100px);
    opacity: 1;
  }

  .lianimate-enter {
    transform: translateY(-50px);
    opacity: 1;
  }

  .lianimate-leave-active,
  .lianimate-enter-active {
    transition: all 0.5s linear;
  }
  .lianimate-leave-to {
    transform: translateX(-100px);
    opacity: 0;
  }

  .icon-position {
    position: absolute;
    top: 30px;
    left: 17px;
    background-color: #eee;
    border-radius: 50%;
  }

  .icons-position {
    position: absolute;
    top: 60px;
    left: 17px;
    border-radius: 50%;
  }

  .text-position {
    position: absolute;
    top: 60px;
    left: 33px;
    border-radius: 50%;
  }

  .head-portrait {
    width: 30px;
    height: 30px;
    border-radius: 50%;
  }

  .display-file {
    width: 100%;
    margin: 5px 0;
  }

  .font-style {
    display: inline-block;
    margin: 0 10px;
    position: relative;
    top: -7.5px;
    line-height: 30px;
    height: 30px;
    font-size: 14px;
    color: #000;
  }

  .comments-position-box {
    position: relative;
    height: 50px;
    border-bottom: 1px solid #aaa;

    .comments-font-style {
      display: inline-block;
      margin: 0 10px;
      position: relative;
      top: -20px;
      max-width: 414px;
      word-wrap: break-word;
      line-height: 30px;
      height: 30px;
      font-size: 14px;
      color: #000;
    }

    .comments-font-styles {
      display: block;
      font-size: 13px;
       margin: 0 10px;
      position: absolute;
      top: 15px;
      left: 30px;
      max-width: 414px;
      word-wrap: break-word;
      line-height: 30px;
      height: 30px;
      color: #000;
    }

    .delete-follow {
      position: absolute;
      top: 0;
      left: 80%;
      transform: translateX(-30px);
      height: 30px;
      background-color: #fff;
      line-height: 30px;
      color: #de1a00;
    }
  }

  .text-box {
    display: inline-block;
    font-size: 18px;
    text-overflow: ellipsis;
    color: #666;
  }

  .footer-font {
    margin: 0 0 0 85%;
    color: #aaa;
    font-size: 14px;
  }

  .box-position {
    position: relative;
    margin-top: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
  }
  textarea {
    margin: 20px 0 20px 2%;
    padding: 5px;
    outline: none;
    border: 1px #aaa solid;
  }

  button {
    position: absolute;
    top: 20px;
    margin-left: 2px;
    height: 27px;
  }
}
</style>